
* {
  box-sizing: border-box;
}
body {
  padding: 0;
  margin: 0;
  text-align: center;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  height: 100vh;
  overflow: hidden;
}
.mint-tabbar {
  height: 50px;
  box-shadow: 0 -1px 0px rgba(34, 25, 25, 0.4);
}
.mint-tabbar .mint-tab-item.is-selected {
    background-color: inherit;
    color: inherit;
}
.mint-tab-item {
  padding: 10px 0;
  color: #999;
}
.mint-tab-item .mint-tab-item-label {
    font-size: 16px;
    line-height: 30px;
}
.mint-header {
  height: 50px;
  font-size: 18px;
  letter-spacing: 0.5em;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .3s;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}
.mask {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  z-index: 2;
  background-color: rgba(59, 62, 68, 0.95);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.mask .mask-img {
    -ms-flex: 1;
        flex: 1;
    background-color: rgba(59, 62, 68, 0.8);
}
.mask .mask-img img {
      width: 100%;
}



